

import Vue from 'vue'
import FastClick from 'fastclick'
import App from './App'
import router from './router/index'

import AjaxPlugin from 'vux/src/plugins/ajax/index.js'


FastClick.attach(document.body)

Vue.config.productionTip = false
Vue.use(AjaxPlugin)

/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount('#app-box')



